color: var(--color4);\r\n}\r\n\r\n.colorgrafica2 {\r\n    background-color: var(--color3);\r\n}\r\n\r\n/* ----------------------------------------2da parte */\r\n\r\n/*===== Vertical Timeline =====*/\r\n\r\n#conference-timeline {\r\n    position: relative;\r\n    max-width: 920px;\r\n    width: 100%;\r\n    margin: 0 auto;\r\n}\r\n\r\n#conference-timeline .timeline-start,\r\n#conference-timeline .timeline-end {\r\n    display: table;\r\n    background: var(--color4);\r\n    padding: 10px 10px;\r\n    color: #fff;\r\n    max-width: 30%;\r\n    width: 100%;\r\n    text-align: center;\r\n    margin: 0 auto;\r\n    z-index: 3;\r\n}\r\n\r\n#conference-timeline .conference-center-line {\r\n    position: absolute;\r\n    width: 3px;\r\n    height: 100%;\r\n    top: 0;\r\n    left: 50%;\r\n    margin-left: -2px;\r\n    background: var(--color4);\r\n    z-index: 1;\r\n}\r\n\r\n#conference-timeline .conference-timeline-content {\r\n    padding-top: 35px;\r\n    padding-bottom: 35px;\r\n}\r\n\r\n.timeline-article {\r\n    width: 100%;\r\n    height: 100%;\r\n    position: relative;\r\n    overflow: hidden;\r\n    margin: 20px 0;\r\n    z-index: 2;\r\n}\r\n\r\n.timeline-article .content-left-container {\r\n    margin-left: 20px;\r\n}\r\n\r\n.timeline-article .content-right-container {\r\n    margin-right: 20px;\r\n}\r\n\r\n.timeline-article .content-left-container,\r\n.timeline-article .content-right-container {\r\n    max-width: 44%;\r\n    width: 90%;\r\n}\r\n\r\n/* .timeline-article .timeline-author {\r\n    display: block;\r\n    font-weight: 400;\r\n    font-size: 14px;\r\n    line-height: 24px;\r\n    color: #242424;\r\n    text-align: right;\r\n} */\r\n\r\n.timeline-article .content-left,\r\n.timeline-article .content-right {\r\n    position: relative;\r\n    width: 100%;\r\n    max-width: 100%;\r\n    border: 1px solid #ddd;\r\n    background-color: #fff;\r\n    box-shadow: 0 1px 3px rgba(0, 0, 0, .03);\r\n    padding: 6px;\r\n}\r\n\r\n.timeline-article p {\r\n    margin: 0 0 0 0px;\r\n    padding: 0;\r\n    font-weight: 400;\r\n    color: #242424;\r\n    font-size: 14px;\r\n    line-height: 24px;\r\n    position: relative;\r\n}\r\n\r\n.timeline-article .content-left-container {\r\n    float: left;\r\n}\r\n\r\n.timeline-article .content-right-container {\r\n    float: right;\r\n}\r\n\r\n.timeline-article .content-left:before,\r\n.timeline-article .content-right:before {\r\n    position: absolute;\r\n    top: 20px;\r\n    font-size: 23px;\r\n    font-family: \"FontAwesome\";\r\n    color: #fff;\r\n}\r\n\r\n/* .timeline-article .content-left:before {\r\n    content: \"\\f0da\";\r\n    right: -8px;\r\n} */\r\n\r\n/* .timeline-article .content-right:before {\r\n    content: \"\\f0d9\";\r\n    left: -8px;\r\n} */\r\n\r\n.timeline-article .meta-date {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 50%;\r\n    width: 30px;\r\n    height: 30px;\r\n    margin-left: -16px;\r\n    color: #fff;\r\n    border-radius: 100%;\r\n    background: var(--color1);\r\n    border-style: solid;\r\n    border-width: 3px;\r\n    border-color: var(--color5);\r\n}\r\n\r\n/* .timeline-article .meta-date .date,\r\n.timeline-article .meta-date .month {\r\n    display: block;\r\n    text-align: center;\r\n    font-weight: 900;\r\n}\r\n\r\n.timeline-article .meta-date .date {\r\n    font-size: 25px;\r\n    line-height: 40px;\r\n}\r\n\r\n.timeline-article .meta-date .month {\r\n    font-size: 12px;\r\n    line-height: 10px;\r\n} */\r\n\r\n/*===== // Vertical Timeline =====*/\r\n\r\n/*===== Resonsive Vertical Timeline =====*/\r\n\r\n@media only screen and (max-width: 830px) {\r\n    #conference-timeline .timeline-start,\r\n    #conference-timeline .timeline-end {\r\n        margin: 0;\r\n    }\r\n    #conference-timeline .conference-center-line {\r\n        margin-left: 0;\r\n        left: 50px;\r\n    }\r\n    .timeline-article .meta-date {\r\n        margin-left: 0;\r\n        left: 20px;\r\n    }\r\n    .timeline-article .content-left-container,\r\n    .timeline-article .content-right-container {\r\n        max-width: 100%;\r\n        width: auto;\r\n        float: none;\r\n        margin-left: 110px;\r\n        min-height: 53px;\r\n    }\r\n    .timeline-article .content-left-container {\r\n        margin-bottom: 20px;\r\n    }\r\n    .timeline-article .content-left,\r\n    .timeline-article .content-right {\r\n        padding: 10px 25px;\r\n        min-height: 65px;\r\n    }\r\n    .timeline-article .content-left:before {\r\n        content: \"\\f0d9\";\r\n        right: auto;\r\n        left: -8px;\r\n    }\r\n    .timeline-article .content-right:before {\r\n        display: none;\r\n    }\r\n}\r\n\r\n@media only screen and (max-width: 400px) {\r\n    .timeline-article p {\r\n        margin: 0;\r\n    }\r\n    .timeline-article p span.article-number {\r\n        display: none;\r\n    }\r\n}\r\n\r\n/*===== // Resonsive Vertical Timeline =====*/\r\n\r\n/* education */\r\n\r\n.contenedu {\r\n    border-style: solid;\r\n    border-width: 1px;\r\n    border-color: var(--color4);\r\n    margin: 0 auto;\r\n}\r\n\r\n.arrowText {\r\n    position: relative;\r\n    margin: 0 auto;\r\n    width: 70%;\r\n    height: 48px;\r\n    background: var(--color4);\r\n    border: solid 1px var(--color5);\r\n    float: right;\r\n}\r\n\r\n.arrowText:before,\r\n.arrowText:after {\r\n    content: \" \";\r\n    position: absolute;\r\n    display: block;\r\n    width: 0;\r\n    height: 0;\r\n}\r\n\r\n/* RIGHT ARROW */\r\n\r\n.arrowRight:before,\r\n.arrowRight:after {\r\n    border-bottom: 25px solid transparent;\r\n    border-top: 25px solid transparent;\r\n    top: -1px;\r\n}\r\n\r\n.arrowRight:after {\r\n    border-left: 16px solid var(--color4);\r\n    right: -15px;\r\n    z-index: 2;\r\n}\r\n\r\n.arrowRight:before {\r\n    border-left: 16px solid var(--color5);\r\n    right: -16px;\r\n    z-index: 1;\r\n}\r\n\r\n.colperfil {\r\n    margin-bottom: 600px;\r\n}\r\n\r\n.cardweb {\r\n    background-color: var(--color4);\r\n}\r\n\r\n/* portafolio */\r\n/*# sourceMappingURL=data:application/json;base64, */"